.container__sentence {
    font-size: 20px;
    width: 250px;
    margin: 5% 0;
    font-weight: bold;
    align-items: center;
    display: flex;
    height: 50px;
    text-shadow: -1px -1px 1px #505c6c, 1px 1px 1px #121c24, 0px 0px 0px #121c24, 0px 0px 0px #000;
}


@media screen and (orientation : landscape) {
    .container__sentence {
        margin: 15% 0;
    }

}



@media all and (min-width: 768px) {
    .container__sentence {
        font-size: 28px;
        width: 400px;
        margin: 15% 0;
    }
}
