.container__maxMinTemperature {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 15%;
    font-size: 16px;
    .unit {
        font-size: 16px;
    }
}

.minTemperature {
    margin-right: 22px;
}

.maxTemperature {
    margin-left: 22px;
}



@media all and (min-width: 768px) {
    .container__maxMinTemperature {
        font-size: 22px;
        .unit {
            font-size: 22px;
        }
    }
}


